.metronome {
    display: inline-flex;
}

#metronome-icon {
    margin-right: 10px;
}

#metronome-avatar {
    margin-right: -4px;
}

#metrInput{
    width: 1cm;
    /* height: 30px; */
    text-align: center;
    font-size: 16px;
}

#picture{
    background-image: url("https://png.pngtree.com/png-vector/20190928/ourmid/pngtree-classic-metronome-icon-cartoon-style-png-image_1753285.jpg");
    background-size: 35px;
    height: 40px;
    width: 40px;
    border-color: black;
    border-radius: 10%;
    /* margin-right: 10px; */
    /* margin-top: 20px; */
}

#plus{
    width: 0;
    height: 0;
    padding: 0px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 2px;
    background-color:  transparent;
    border-style: solid;
    border-left-width: 10px;
    border-right-width: 10px;
    border-bottom-width: 20px;
    border-top-width: 0px;
    border-left-color: transparent;
    border-right-color: transparent;
    border-bottom-color: gray;
}

#minus {
    width: 0;
    height: 0;
    padding: 0px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 2px;
    background-color:  transparent;
    border-style: solid;
    border-left-width: 10px;
    border-right-width: 10px;
    border-bottom-width: 0px;
    border-top-width: 20px;
    border-left-color: transparent;
    border-right-color: transparent;
    border-top-color: gray;
}


